import { useContext, useState, useEffect } from "react";
import { Breadcrumb, Avatar, Typography, Form, Button, message } from "antd";
import withAuth from "../../HOC/withAuth";
import { AuthContext } from "../../Store/AuthContext";
import { brightColor, mainColor, textColor } from "../../Utils/colors";
import { Card, FlexContainer, FormInput, FormPassword } from "../../Components";
import { FiBook, FiMail, FiLock, FiNavigation } from "react-icons/fi";
import { useUpdateData } from "../../Hooks";
import { setInput } from "../../Utils/myFormat";

const { Text, Title } = Typography;

const ListProfile = ({ icon, value }) => (
  <Text style={{ color: textColor, fontSize: 18 }}>
    {icon} {value}
  </Text>
);

const Profile = () => {
  const [email, setEmail] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setconfirmPassword] = useState("");
  const { signOut } = useContext(AuthContext);
  const authContext = useContext(AuthContext);

  const [form] = Form.useForm();

  const [dataUpdate, setDataUpdate] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess, loading } = useUpdateData(
    dataUpdate.url,
    dataUpdate.data,
    dataUpdate.trigger
  );

  const handleChangePassword = async () => {
    if (!email || !newPassword || !confirmPassword) {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    if (newPassword.length < 8) {
      message.warn("New Password panjang minimal 8 karakter!");

      return null;
    }

    if (confirmPassword.length < 8) {
      message.warn("Confirm New Password panjang minimal 8 karakter!");

      return null;
    }

    if (newPassword !== confirmPassword) {
      message.warn("Confirm New Password tidak sesuai!");

      return null;
    }

    const dataChangePassword = {
      user_id: authContext.userId,
      email: email,
      password: confirmPassword,
      active: 1,
      role_id: authContext.userRole,
      employe_id: authContext.userEmpId,
    };

    setDataUpdate({
      url: "users/edit",
      data: dataChangePassword,
      trigger: new Date().getTime(),
    });
  };

  useEffect(() => {
    if (onSuccess) {
      message.success("Berhasil update password");
      setTimeout(() => {
        signOut();
      }, 1000);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onSuccess]);

  useEffect(() => {
    setEmail(authContext.userEmail);
    form.setFieldsValue({ email: authContext.userEmail });
  }, [authContext.userEmail, form]);

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>Profile</Breadcrumb.Item>
      </Breadcrumb>
      <div>
        <Card>
          <FlexContainer style={{ justifyContent: "flex-start" }}>
            <div style={{ marginRight: 30 }}>
              <Avatar
                size={140}
                style={{
                  color: brightColor,
                  backgroundColor: mainColor,
                  textTransform: "uppercase",
                  fontSize: 50,
                }}
              >
                {authContext.userName.substr(0, 1)}
              </Avatar>
            </div>
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                marginTop: 16,
              }}
            >
              <Text
                style={{
                  color: mainColor,
                  fontSize: 24,
                  fontWeight: "bold",
                  textTransform: "uppercase",
                }}
              >
                {authContext.userName}
              </Text>
              <ListProfile icon={<FiBook />} value={authContext.userEmpId} />
              <ListProfile icon={<FiMail />} value={authContext.userEmail} />
            </div>
          </FlexContainer>
          <div style={{ marginTop: 40 }}>
            <Title level={3} type="secondary" style={{ marginBottom: 30 }}>
              Change Password
            </Title>
            <Form form={form} name="changePassword" autoComplete="off">
              <FormInput
                label="Email"
                requiredLabel
                name="email"
                required
                message="Please input your email!"
                prefix={<FiLock color="rgba(0,0,0,.25)" />}
                size="large"
                placeholder="enter your email"
                onChange={(e) => setInput(e, setEmail)}
              />
              <FormPassword
                label="New Password"
                requiredLabel
                name="newpassword"
                required
                message="Please input new password!"
                prefix={<FiLock color="rgba(0,0,0,.25)" />}
                size="large"
                placeholder="enter new password"
                onChange={(e) => setInput(e, setNewPassword)}
              />
              <FormPassword
                label="Confirm Password"
                requiredLabel
                name="confirmpassword"
                required
                message="Please input confirm password!"
                prefix={<FiLock color="rgba(0,0,0,.25)" />}
                size="large"
                placeholder="enter confirm password"
                onChange={(e) => setInput(e, setconfirmPassword)}
                onPressEnter={handleChangePassword}
              />
              <Form.Item>
                <Button
                  type="primary"
                  shape="round"
                  icon={<FiNavigation style={{ marginRight: "6px" }} />}
                  size="large"
                  onClick={handleChangePassword}
                  loading={loading}
                  htmlType="submit"
                >
                  Change Password
                </Button>
              </Form.Item>
            </Form>
          </div>
        </Card>
      </div>
    </div>
  );
};

export default withAuth(Profile);
